import React from 'react';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import {View} from '@vkontakte/vkui';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import Tabbar from '@vkontakte/vkui/dist/components/Tabbar/Tabbar';
import Epic from '@vkontakte/vkui/dist/components/Epic/Epic';
import TabbarItem from '@vkontakte/vkui/dist/components/TabbarItem/TabbarItem';
import Icon28Search from '@vkontakte/icons/dist/28/search';
import Search from '@vkontakte/vkui/dist/components/Search/Search';
import Icon28Notifications from '@vkontakte/icons/dist/28/notification';
import Icon28Messages from '@vkontakte/icons/dist/28/messages';
import Icon28More from '@vkontakte/icons/dist/28/more';
import Icon28Newsfeed from '@vkontakte/icons/dist/28/newsfeed';


const users = [
    {id: 3201, name: "Влад Анесов"},
    {id: 3273, name: "Вадим Дорохов"},
    {id: 3205, name: "Андрей Абрамов"},
    {id: 3282, name: "Александр Колобов"},
    {id: 3283, name: "Артур Стамбульцян"},
    {id: 3284, name: "Илья Таратухин"},
    {id: 3285, name: "Роман Захаров"},
    {id: 3286, name: "Михаил Андриевский"},
    {id: 3287, name: "Тарас Иванов"},
    {id: 3288, name: "Илья Гришин"},
    {id: 3117, name: "Илья Пеняев"},
    {id: 3118, name: "Иван Барышев"},
    {id: 3119, name: "Иван Гусев"},
    {id: 3120, name: "Макс Павлов"},
    {id: 3121, name: "Антон Циварев"},
    {id: 3122, name: "Юля Брук"},
    {id: 3123, name: "Андрей Рогозов"},
    {id: 3124, name: "Андрей Новосельский"}
];
    let fetchedUser = "Чёрт";
class Nav extends React.Component {
    constructor (props) {
        super(props);
        this.state = {
            activeStory: 'feed'
        };

        this.onStoryChange = this.onStoryChange.bind(this);
    }


    onStoryChange (e) {
        this.setState({ activeStory: e.currentTarget.dataset.story })
    }

    render () {

        return (

            <Epic activeStory={this.state.activeStory} tabbar={
                <Tabbar>
                    <TabbarItem
                        onClick={this.onStoryChange}
                        selected={this.state.activeStory === 'feed'}
                        data-story="feed"
                        text="Рейтинг"
                    ><Icon28Newsfeed /></TabbarItem>
                    <TabbarItem
                        onClick={this.onStoryChange}
                        selected={this.state.activeStory === 'discover'}
                        data-story="discover"
                        text="Условия"
                    ><Icon28Search /></TabbarItem>
                    <TabbarItem
                        onClick={this.onStoryChange}
                        selected={this.state.activeStory === 'messages'}
                        data-story="messages"
                        text="Виджет"
                    ><Icon28Messages /></TabbarItem>
                    <TabbarItem
                        onClick={this.onStoryChange}
                        selected={this.state.activeStory === 'notifications'}
                        data-story="notifications"
                        text="Настройки"
                    ><Icon28Notifications /></TabbarItem>
                    <TabbarItem
                        onClick={this.onStoryChange}
                        selected={this.state.activeStory === 'more'}
                        data-story="more"
                        text="Фичи"
                    ><Icon28More /></TabbarItem>
                </Tabbar>
            }>
                <View id="feed" activePanel="feed">
                    <Panel id="feed">
                        <PanelHeader>Текущий рейтинг</PanelHeader>
                        <Search theme="default" />
                        {this.props.fetchedUser &&
                        <div>
                        <p>Йоу, {fetchedUser = this.props.fetchedUser}
                            { alert(fetchedUser.last_name)}
                            `${fetchedUser.first_name} ${fetchedUser.last_name}`, поиск пока не робит <br/></p>
                        </div>}
                          <h1>Здесь будет таблица рейтинга</h1>

                    </Panel>
                </View>
                <View id="discover" activePanel="discover">
                    <Panel id="discover">
                        <PanelHeader>Условия</PanelHeader>
                    </Panel>
                </View>
                <View id="messages" activePanel="messages">
                    <Panel id="messages">
                        <PanelHeader>Виджет</PanelHeader>
                    </Panel>
                </View>
                <View id="notifications" activePanel="notifications">
                    <Panel id="notifications">
                        <PanelHeader>Настройки</PanelHeader>
                    </Panel>
                </View>
                <View id="more" activePanel="more">
                    <Panel id="more">
                        <PanelHeader>Фичи</PanelHeader>
                    </Panel>
                </View>
            </Epic>
        )
    }
}


export default Nav;